import React, { useState, useEffect } from "react";
import "./App.css";
import "antd/dist/antd.css";
import { RentInfoTable, schemas } from "./components";
import { Spin } from "antd";

const App = () => {
  const [isFetched, setIsFetched] = useState(false);
  const [data, setData] = useState([]);

  useEffect(() => {
    (async () => {
      setIsFetched(false);
      const data = await fetch("/api/properties").then((response) =>
        response.json()
      );
      setData(data);
      setIsFetched(true);
    })();
  }, []);

  if (!isFetched) return <Spin />;
  return (
    <div className="p-5">
      <RentInfoTable data={data} schemas={schemas.rentInfo} />
    </div>
  );
};

export default App;
